@import url('https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#root {
  background-color: #111;
}

.all-content {
  font-family: 'Luckiest Guy', cursive;
  max-width: 1200px;
  background-color: white;
  margin: 0 auto;
  min-height: 100%;
}

header {
  background-color: #3e6141;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.title {
  font-size: 2.5rem;
  padding-top: 6px;
}

.logo {
  width: 120px;
}

.cards {
  display: flex;
  justify-content: space-evenly;
}

.cards .card {
  display: flex;
  flex-direction: column;
  color: white;
  background-color: #111;
  padding: 25px;
  align-items: center;
  border-radius: 5px;
  transition: all 0.2s;
  box-shadow: 3px 3px 8px 5px rgba(0,0,0,0.7);
}

.card .gender {
  display: none;
}

.card:hover {
  transform: scale(1.15);
  box-shadow: 1px 1px 8px 3px rgba(0,0,0,0.7);
}

.card.selected {
  transform: scale(1.15);
  background-color: #000;
  box-shadow: 1px 1px 8px 3px rgba(0,0,0,0.7);
}

.cards img {
  width: 125px;
  margin-bottom: 15px;
}

.playlists {
  display: flex;
  flex-direction: column;
  margin-bottom: 100px;
}

.playlists p {
  font-size: large;
  text-align: center;
  margin-bottom: 30px;
}

footer{
  text-align: center;
  background-color: #111;
  padding-top: 5px;
  color: white;
}

footer p {
  padding: 10px 0;
}

footer p a {
  text-decoration: none;
}

button, .card {
  cursor: pointer;
}

.timer {
  font-size: 3rem;
  text-align: center;
  margin: 30px 0;
}

.timerHandlers {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.timerHandlers p {
  font-size: large;
}

.timerHandlers div {
  display: flex;
  margin-top: 10px;
}

#minutesInpt {
  margin-right: 10px;
  width: 150px;
  font-size: 18px;
  padding: 5px;
}

.minutesBtn, .savePresetBtn, .presetBtn {
  padding: 10px 20px 5px 20px;
  font-family: 'Luckiest Guy', cursive;
  margin-right: 10px;
}

.buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.buttons button {
  margin-right: 15px;
  font-family: 'Luckiest Guy', cursive;
  padding: 10px 20px 5px 20px;
}

@media screen and (max-width: 750px) {
  header {
    width: 100%;
  }
  .title {
    font-size: 1.75rem;
  }
  .logo {
    width: 100px;
  }
  .cards .card {
    padding: 15px 10px;
    font-size: 1rem;
    box-shadow: none;
  }
  .card img {
    width: 75px;
  }

  footer {
    font-size: 14px;
  }
  .buttons button {
    margin-right: 7px;
    padding: 10px 15px 5px 15px;
  }
  .buttons .playBtn {
    padding-bottom: 8px;
  }
  .timer {
    font-size: 2.5rem;
  }
  #minutesInpt {
    width: 100px;
    font-size: 14px;
  }
  .savePresetBtn {
    display: none;
  }
}

@media screen and (max-width: 500px) {
  .cards .card {
    background: none;
  }
  .card .gender {
    display: block;
    color: black;
  }
  .card span {
    display: none;
  }
}